import { NavLink } from "react-router-dom"
import styles from "./Footer.module.scss"

const Information = () => {
    return (
        <div className={styles.information}>
            <section>
                <h3></h3>
                <ul>
                    <li><NavLink to="/"></NavLink></li>
                    <li><NavLink to="/"> </NavLink></li>
                    <li><NavLink to="/"></NavLink></li>
                </ul>
            </section>
        </div>
    )
}

export default Information